import { useParams } from 'react-router-dom'
import { useState, useRef } from 'react';
import './Styles/Comments.css'
import { useContext } from 'react';
import {httpContext} from '../Components/httpHookContext';

export default function Comments(){

    const [addComment, setAddComment]=useState(false);



    const {quoteId}=useParams();
    const myHttpController=useContext(httpContext);
    const quotes= myHttpController.quotes;
    const quoteIndex= quotes?.findIndex((q)=> {return q.id==quoteId;});
    let quote;
    if(quoteIndex!==-1)quote=quotes[quoteIndex];
    const myComment=useRef();

    console.log(quote?.comments);
    console.log("yaya"+(quote?.comments!=[]?true:false));

    return <><h2 className='userCommentsTitle'>User Comments</h2>
    {addComment?<><h3 className='addCommentTitle'>Your Comment</h3><textarea className='commentTextStyle' rows='4' ref={myComment}></textarea></>:null}
    <button className='addCommentsButton' onClick={()=>{if(addComment){if(myComment.current.value!=='')myHttpController.addComment(quoteId,myComment.current.value); setAddComment(false)}
       else setAddComment(true)}}>Add Comment</button> 
    {(quote && quote.comments && quote.comments.length!=0)?quote.comments.map((c,index)=>{return <p key={index} className='commentStyle'>{c}</p>}):
    (myHttpController.pending?<p style={{color:"black",marginTop:"4vh"}}>Loading comments...</p>:<p style={{color:"black",marginTop:"4vh"}}>No comments were added yet.</p>)}
    </>
}