import { Routes, Route, useNavigate, useLocation } from 'react-router-dom';
import Header from './Components/Header';
import './App.css';
import { useEffect } from 'react';
import QuotesPage from './Pages/QuotesPage';
import Comments from './Pages/Comments';
import QuoteFullScreen from './Pages/QuoteFullScreen';
import React, {Suspense} from 'react';





function App() {

  const AddQuotePage= React.lazy(()=> import('./Pages/AddQuotePage'))


  const NotFound= React.lazy(()=>import('./Pages/NotFound'))

  const navigate= useNavigate();
  const location=useLocation();
  const currentPath=location.pathname;
  console.log('currentPath');

  useEffect(()=>{
    if(currentPath=='/')
    navigate("/quotes");
  },[])

 

  return <><Header></Header>
  <Suspense fallback={<div className='centered'>Loading...</div>}>
    <Routes>
      <Route path='/'></Route>
      <Route path='/quotes' element={<QuotesPage></QuotesPage>}></Route>
      <Route path='/quotes/:quoteId' element={<QuoteFullScreen></QuoteFullScreen>}>
        <Route path='' element={<button className='loadCommentsButton' onClick={()=>{ navigate(currentPath+'/comments');}}>Load Comments</button>}></Route>
        <Route path='comments' element={<Comments></Comments>}></Route>
      </Route>
      <Route path='/addquote' element={<AddQuotePage></AddQuotePage>}></Route>
      <Route path='*' element={<NotFound></NotFound>}></Route>
    </Routes>
    </Suspense>



  </>;
}

export default App;
