import './Styles/Header.css'
import { Link, useLocation, useNavigate } from 'react-router-dom'

export default function Header(){

    const location = useLocation();
    const pathname= location.pathname;

    return <div className="headerdiv">

        <h1 className='leftHeaderPart'>Great Quotes</h1>
        <div className='menuDiv'>
            <Link to='/quotes' className={`LinkStyle ${pathname === '/quotes' ? 'LinkStyleSelected' : ''}`}><h2>All quotes</h2></Link>
            <Link to='/addquote' className={`LinkStyle ${pathname === '/addquote' ? 'LinkStyleSelected' : ''}`}><h2>Add quote</h2></Link>
        </div>
    </div>
}