import './Styles/QuotesPage.css'
import { Link, useLocation, useNavigate } from 'react-router-dom';
import { useContext } from 'react';
import {httpContext} from '../Components/httpHookContext';
import NoQuotes from '../Components/NoQuotes';

export default function QuotesPage(){

    
 
    const navigate= useNavigate();
    const location =useLocation();
    const queryParams= new URLSearchParams(location.search);
    const isSortingAscendent=queryParams.get('sort')==='ascending';
    console.log(isSortingAscendent);

    const myHttpController=useContext(httpContext);
    
    const quotes=myHttpController.quotes;
  
    
    

   

   


    if (!myHttpController.pending && quotes && quotes.length==0)return <NoQuotes/>;


    return <div className='quotesPage__mainDiv'>
        <div className='quotesPage__mainDiv-2'>
        <button className='sortButton' onClick={()=>{ navigate('/quotes?sort=' + (!isSortingAscendent?'ascending':'descending'))}}>{isSortingAscendent?'Sort Descending':'Sort Ascending'}</button>
        <div className='lineDiv'></div>
        
            {
           myHttpController.pending ? <p>Loading quotes...</p> :
           (isSortingAscendent ? 
             quotes.length !== 0 && 
             quotes.sort((a, b) => a.text.localeCompare(b.text)).map((q) => {
               return <div className='quoteDiv' key={q.id}>
                 <div className='quoteDiv-2'>
                   <p className='quoteText'>{q.text}</p>
                   <p className='quoteAuthor'>{q.author}</p>
                 </div>
                 <Link to={'/quotes/'+q.id}><button className='viewFullScreenButton'>View Fullscreen</button></Link>
               </div>
             }) :
             quotes.length !== 0 &&
             quotes.sort((a, b) => b.text.localeCompare(a.text)).map((q) => {
               return <div className='quoteDiv' key={q.id}>
                 <div className='quoteDiv-2'>
                   <p className='quoteText'>{q.text}</p>
                   <p className='quoteAuthor'>{q.author}</p>
                 </div>
                 <Link to={'/quotes/'+q.id}><button className='viewFullScreenButton'>View Fullscreen</button></Link>
               </div>
             }))
            }
            
            
           {/* <div className='quoteDiv-2'><p className='quoteText'>This is test!</p><p className='quoteAuthor'>Test</p></div>
            <button className='viewFullScreenButton'>View Fullscreen</button> */}
        
        </div>  
</div>
}