import React from 'react';
import useHttp from '../Hooks/use-http';
import useQuotes from '../Hooks/use-quotes';


const httpContext= React.createContext();

const HttpContextProvider=(props)=>{
    const httpController=useQuotes();
    //const httpController=useHttp();
    return <httpContext.Provider value={httpController}>{props.children}</httpContext.Provider>
}

export {httpContext, HttpContextProvider};