import './Styles/QuoteFullScreen.css'
import { Outlet, useParams, useNavigate } from 'react-router-dom'
import { useContext } from 'react';
import {httpContext} from '../Components/httpHookContext';

export default function QuoteFullScreen(){


  
    
    const navigate= useNavigate();
    const myHttpController= useContext(httpContext);
    const quotes=myHttpController.quotes;
    

    const {quoteId}=useParams();
    console.log(quoteId);
    console.log(quotes);
    let quote;
    const quoteIndex= quotes?.findIndex((q)=>{
        return q.id==quoteId;
    });
     if(quoteIndex!==-1)quote=quotes[quoteIndex];
   
    if(!quote)quote={id:quoteId,text:"No quote found!",author:""}

    return <div className='helperFullQuoteDiv'>  <div className="mainFullQuoteDiv">
            <div className='helperDiv'>
                <p className='quotePharagraph'>{quote?.text}</p>
                <p className='authorPharagraph'>{quote?.author}</p>
            </div>
         </div>
         {quote.text!='No quote found!' && <Outlet/>}
         
         </div>
         
}